@use "../src/scss/abstracts/mixins";

.breadcrumbs {
  margin: 50px 0;
  
  &__list {
    @include mixins.excerpt(1);
    width: 95%;
    overflow: hidden;
  }

  &__item {
    display: inline-block;

    &:not(:last-child):after {
      content: "/";
      display: inline-block;
      margin: 0 7px;
      position: relative;
      width: 0.2em;
    }
  }

  &__link {
    color: black;

    &[aria-current="page"] {
      color: inherit;
      font-weight: 400;
      text-decoration: none;
    }
  }
}